import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { bowCAs, lightPAs, multMult } from "../config";

export const AloyBuffs = [
  {
    src: "Elemental Skill",
    desc: (_, char, { charBC }) => (
      <>
        When a Freeze Bomb or Chillwater Bomblet hits an opponent, Aloy receives
        1 Coil stack, increasing her <LGreen>Normal Attack DMG</LGreen>.{" "}
        <Red>Total DMG Bonus: {AloyBuffs[0].bnValue(char, charBC)}%.</Red>
      </>
    ),
    isGranted: () => true,
    affect: "self",
    maxStacks: 3,
    addBnes: (_, bonuses, __, ___, _ph1, char, charBC) => {
      bonuses.NA.DMG += AloyBuffs[0].bnValue(char, charBC);
    },
    bnValue: (char, charBC) => {
      const level = char["Elemental Skill"];
      const stacks = charBC[1].activated ? 5 : charBC[0].value;
      return Math.round(584.6 * multMult[5][level] * stacks) / 100;
    }
  },
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        When Aloy has 4 Coil stacks, further increases the <LGreen>DMG</LGreen>{" "}
        dealt by her <LGreen>Normal Attacks</LGreen> and converts her{" "}
        <LGreen>Normal Attack DMG</LGreen> to <LGreen>Cryo DMG</LGreen>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Aloy receives the Coil effect from Frozen Wilds, her{" "}
        <LGreen>ATK</LGreen> is increased by <Green>16%</Green>, while nearby
        party members' <LGreen>ATK</LGreen> is increased by <Green>8%</Green>.
        This effect lasts 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: (ATRs, _, __, ___, toSelf) => {
      ATRs["ATK%"] += toSelf ? 16 : 8;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        When Aloy is in the Rushing Ice state conferred by Frozen Wilds, her{" "}
        <LGreen>Cryo DMG Bonus</LGreen> increases by <Green>3.5%</Green> every
        1s, up to <Green>35%</Green>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    maxStacks: 10,
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["Cryo DMG Bonus"] += 3.5 * stacks;
    }
  }
];

