import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { mediumPAs } from "../config";

export const ThomaBuffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When your current active character obtains or refreshes a Blazing
        Barrier, this character's <LGreen>Shield Strength</LGreen> will increase
        by <Green>5%</Green> for 6s. This effect can be triggered once every 0.3
        seconds. Max <Green>5</Green> stacks.
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    maxStacks: 5,
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: (ATRs, _, __, stacks) => {
      ATRs["Shield Strength"] += stacks * 5;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: (_, __, { ATRs }) => (
      <>
        <LGreen>DMG</LGreen> dealt by{" "}
        <LGreen>Crimson Ooyoroi's Fiery Collapse</LGreen> is increased by{" "}
        <Green>2.2%</Green> of Thoma's <LGreen>Max HP</LGreen>.{" "}
        <Red>DMG Bonus: {ThomaBuffs[1].bnValue(ATRs)} (base).</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    bnValue: (ATRs) => Math.round(ATRs.HP * 0.022)
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        When a Blazing Barrier is obtained or refreshed, the{" "}
        <LGreen>DMG</LGreen> dealt by all party members'{" "}
        <LGreen>Normal, Charged, and Plunging Attacks</LGreen> is increased by{" "}
        <Green>15%</Green> for 6s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: (_, bonuses) => {
      bonuses.NA.DMG += 15;
      bonuses.CA.DMG += 15;
      bonuses.PA.DMG += 15;
    }
  }
];

