import { Green, LGreen, Red } from "../../../styledCpns/DataDisplay";
import { mapToLevels } from "../../misc";
import { lightPAs, multMult } from "../config";

export const KokomiBuffs = [
  {
    src: "Elemental Burst",
    desc: (_, char, { ATRs, charBC }) => {
      const result = KokomiBuffs[0].bnValue(char, ATRs, charBC[1].activated);
      return (
        <>
          Kokomi's{" "}
          <LGreen>Normal Attack, Charged Attack and Bake-Kurage DMG</LGreen> are
          increased based on her <LGreen>Max HP</LGreen>.{" "}
          <Red>
            Normal Attack DMG Bonus (base): {result.NA}. Charged Attack DMG
            Bonus (base): {result.CA}. Bake-Kurage DMG Bonus (base): {result.ES}
            .
          </Red>
        </>
      );
    },
    isGranted: () => true,
    affect: "self",
    bnValue: (char, { HP, "Healing Bonus": HB }, A4on) => {
      const lv = char["Elemental Burst"] + (char.constellation >= 3 ? 3 : 0);
      const EB_BuffMults = {
        NA: 4.84,
        CA: 6.78,
        ES: 7.1
      };
      const result = {};
      ["NA", "CA", "ES"].forEach((type) => {
        let mult = EB_BuffMults[type] * multMult[2][lv];
        if (type !== "ES" && A4on) mult += HB * 0.15;
        result[type] = Math.round((HP * mult) / 100);
      });
      return result;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        During Nereid's Ascension, the{" "}
        <LGreen>Normal and Charged Attack DMG Bonus</LGreen> Kokomi gains based
        on her <LGreen>Max HP</LGreen> will receive a further increase based on{" "}
        <Green>15%</Green> of her <LGreen>Healing Bonus</LGreen>.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        During Nereid's Ascension, Kokomi's <LGreen>Normal Attack SPD</LGreen>{" "}
        is increased by <Green>10%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Normal ATK SPD"] += 10;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        During Nereid's Ascension, Kokomi gains a <Green>40%</Green>{" "}
        <LGreen>Hydro DMG Bonus</LGreen> for 4s after her Normal and Charged
        Attacks heal a character with 80% or more HP.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: (ATRs) => {
      ATRs["Hydro DMG Bonus"] += 40;
    }
  }
];

